html {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
}

.app {
  height: 100vh;
  /* position: relative; */
}

.app__content {
  padding: 0 !important;
  padding-top: 76px !important;
}

.header {
  background-color: transparent !important;
  box-shadow: none !important;
  padding: 10px 5px;
}

.header__logo {
  width: 135px;
}

.bottom-nav {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  box-shadow: 0px 2px 13px 1px rgba(0, 0, 0, 0.217) !important;
}

.bottom-nav__link {
  flex: 1;
  display: flex;
  justify-content: center;
  padding: 0 !important;
}

/* Create Recipe */

.create-recipe {
  /* position: absolute; */
  left: 0;
  padding: 0 !important;
}

.create-recipe__row {
  margin-top: 5px;
  margin-bottom: 15px;
}

.create-recipe__row:last-child {
  margin-bottom: 76px;
}

.create-recipe__row h2 {
  padding-left: 30px;
}

.create-recipe__slider {
  display: flex;
  overflow-x: scroll;
}

.food-card {
  width: 200px;
  max-width: 200px;
  min-width: 200px;
  margin: 10px;
}

.food-card .MuiCardContent-root {
  padding: 4px 16px;
}

